import { mapState } from 'vuex'
import store from '@/store'

// store/index.jsvuexstate
let $tStoreKey = []
try {
  $tStoreKey = store.state ? Object.keys(store.state) : []
} catch(e) {
  
}

module.exports = {
  beforeCreate() {
    this.$t.vuex = (name, value) => {
      this.$store.commit('$tStore', {
        name, value
      })
    }
  },
  computed: {
    // vuexstatemixin
    ...mapState($tStoreKey)
  }
}